import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/meals.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/meals/:id',
    name: 'meals',
    component: () => import(/* webpackChunkName: "about" */ '../components/mealsInfo.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
