import { createStore } from 'vuex'

export default createStore({
  state: {
    meals: null,
    mealInfo: null
  },
  getters: {
  },
  mutations: {
  },
  actions: {
    getFetchMeals({ state }) {
      fetch("https://www.themealdb.com/api/json/v1/1/search.php?f=b")
        .then(res => res.json())
        .then(data => state.meals = data.meals)
    },
    getSoloMeals({ state }, id) {
      fetch(`https://www.themealdb.com/api/json/v1/1/lookup.php?i=${id}`)
        .then(res => res.json())
        .then(data => state.mealInfo = data.meals[0] )
    },
  },
  modules: {
  }
})
